.rf-view-textarea-value .el-textarea__inner {
  padding: 6px 0;
  border: none;
  resize: none;
}
.rf-view-textarea-value .el-textarea__inner[disabled="disabled"] {
    background: inherit;
    color: inherit;
    cursor: text;
}
.el-textarea /deep/ .el-textarea__inner {
  padding-left: 10px;
  padding-right: 10px;
  word-break: break-all;
}
.el-textarea /deep/ .el-textarea__inner::-webkit-scrollbar {
    width: 4px;
    height: 4px;
}
.el-textarea /deep/ .el-textarea__inner::-webkit-scrollbar-thumb {
      border-radius: 20px;
      background: #a5a5a5;
      -webkit-box-shadow: inset 0 0 6px rgba(204, 204, 204, .3);
              box-shadow: inset 0 0 6px rgba(204, 204, 204, .3);
}
.rf-view-textarea-value /deep/ .el-textarea__inner {
  padding: 6px 0 0;
  line-height: 24px;
}
