.rf-form-item {
  position: relative;
  margin: 15px 0;
  min-height: 36px;
}
.rf-form-item:first-child {
    margin-top: 0;
}
.rf-form-item:last-child {
    margin-bottom: 0;
}
.rf-form-item.rf-has-hook > .rf-tag-form {
    margin-right: 30px;
}
.rf-form-item .rf-tag-label {
    float: left;
    position: relative;
    margin-top: 8px;
    width: 100px;
    font-size: 14px;
    color: #313238;
    text-align: right;
    word-wrap: break-word;
    word-break: break-all;
}
.rf-form-item .rf-tag-label.required:before {
      content: '*';
      position: absolute;
      top: 0px;
      right: -10px;
      color: #F00;
      font-family: "SimSun";
}
.rf-form-item.show-label > .rf-tag-form {
    margin-left: 120px;
}
.rf-form-item .rf-tag-hook {
    position: absolute;
    top: 8px;
    right: 0;
    z-index: 1;
}
.rf-form-item .rf-view-value {
    display: inline-block;
    height: 36px;
    line-height: 36px;
    font-size: 14px;
    word-wrap: break-word;
    word-break: break-all;
}
.rf-form-item .el-table__empty-text {
    line-height: 20px;
    width: 100%;
}
